.card-item {
  position: absolute;
  flex: 1 0 100px;
  background: white;
  padding: 10px;
  bottom: 20px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease-out; }

.hotspot:hover + .card-item {
  display: block;
  visibility: visible;
  opacity: 1;
  z-index: 2; }

