.player_screen__wrapper {
  width: 100%;
  position: relative;
  -webkit-box-shadow: 11px 11px 53px -6px rgba(0,0,0,0.75);
  overflow: hidden;
  // border: 1.5px solid #000
  position: relative;
  z-index: 5;
  background: #000; }


.player_screen {
  opacity: 1;
  transition: opacity 1s ease-in;
  width: 100%;
  position: relative; }


.player_screen__wrapper--loading {
  opacity: 0; }

.controls {
  display: flex;
  position: absolute;
  bottom: 0;
  width: 100%;
  background: linear-gradient(0deg, #000 10%, #555 100%);
  opacity: 0.8;
  height: 50px;
  transform: translateY(44px);
  transition: 0.5s ease 0.1s; }

.player_screen__wrapper:hover .controls {
  transform: translateY(0); }
